  .circle-wrap .inside-circle {
    width: 130px;
    height: 130px;
    border-radius: 50%;
    background: #fff;
    line-height: 130px;
    text-align: center;
    margin-top: 10px;
    margin-left: 10px;
    position: absolute;
    z-index: 100;
    font-weight: 700;
    font-size: 2em;
  }



  /*NEW TUT*/

  .circular-chart {
    display: block;
    margin: 10px auto;
    max-width: 80%;
    max-height: 250px;
  }
  
  .circle {
    stroke: #4CC790;
    fill: none;
    stroke-width: 2.8;
    stroke-linecap: round;
    -webkit-animation: progress 1s ease-out forwards;
            animation: progress 1s ease-out forwards;
  }
  
  @-webkit-keyframes progress {
    0% {
      stroke-dasharray: 0 100;
    }
  }
  
  @keyframes progress {
    0% {
      stroke-dasharray: 0 100;
    }
  }



  #circle {
    width: 100%;
    height: 100%;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
  }
  #circle-percentage {
    position: absolute;
    color: #444;
    top: -3em;
    left: 0.3em;
    right: 0;
    font-size: 4em;
  }


  
