.options > * {
  width: 80vw;
  height: 60vh;
  max-width: 100em;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 60rem;
  margin: 0 auto;
  padding: 2rem;
  font-size: 62.5%;
  }
  
  .flex-column {
    display: flex;
    flex-direction: column;
  }
  
  .flex-center {
    justify-content: center;
    align-items: center;
  }
  
  .justify-center {
    justify-content: center;
  }
  
  .text-center {
    text-align: center;
  }
  
  .hidden {
    display: none;
  }

/* FORMS */
form {
    width: 50vw;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

ul {
    list-style-type: none;
}

ul li {
    margin: 5px;
}
