.options-container {
    display: flex;
    border-radius: 2vh;
    margin-bottom: 0.5rem;
    font-size: 1.8rem;
    border: 0.1rem solid #287233;
    background-color: white;
  }
  
  .options-container:hover {
    cursor: pointer;
    border-radius: 2vh;
    box-shadow: 0 0.4rem 1.4rem 0 #287233;
    -webkit-transform: translateY(-0.1rem);
            transform: translateY(-0.1rem);
    transition: -webkit-transform 150ms;
    transition: transform 150ms;
    transition: transform 150ms, -webkit-transform 150ms;
  }
  
  .options-prefix {
    /*padding: 1.5rem 2.5rem;*/
    padding: 1.5rem;
    width: 30vw;
    background-color: #3fa04d;
    border-radius: 2vh;
    color: white;
    font-size: 62.5%;
  }
  
  .options-text {
    padding: 1.5rem;
    width: 100vw;
    font-size: 62.5%;
  }

  #circle-space {
    display: flex;
    justify-content: center;
    text-justify: auto;
    height: 30vh;
    min-height: 0.84em;

  }
  
  /* HUD */
  
  #hud {
    display: flex;
    justify-content: space-between;
    text-justify: auto;
    height: 20vh;

  }
  
  .hud-prefix {
    text-align: center;
    padding-top: 10vh;
    /*font-size: 2rem;*/
    font-size: calc(1em + 1vw);

  }
  
  .hud-main-text {
    text-align: center;
    font-size: calc(2em + 5vh);

  }
  
#progressBar {
    width: calc(4em + 25vw);
    height: 4vh;
    border: 0.1rem #ffffff;
    margin-top: 7.5vh;
    margin-left: 0;
    border-radius: 3vh;
    justify-content: flex-start;
  }
  
#progressBarFull {
    height: 4vh;
    background-color: #3fa04d;
    width: 0%;
    margin-left: 0.1rem;
    top:0; bottom:0;
    border-radius: 3vh;
  }
  

  .OwnInput {
    width: calc(1em + 15vh);
    padding: 1rem;
    font-size: 1.8rem;
    border: none;
    box-shadow: 0 0.1rem 1.4rem 0 #3fa04d;
  }
  
  .OwnInput::-webkit-input-placeholder {
    color: #aaa;
  }
  
  .OwnInput:-ms-input-placeholder {
    color: #aaa;
  }
  
  .OwnInput::placeholder {
    color: #aaa;
  }

  /* Submit */
.OwnSubmit {
  font-size: 1.8rem;
  padding: 1rem 0;
  width: 20rem;
  text-align: center;
  border: 0.1rem solid #3fa04d;
  margin-bottom: 1rem;
  text-decoration: none;
  color: #4f7432;
  background-color: white;
  margin: auto;
}

.OwnSubmit:hover {
  cursor: pointer;
  box-shadow: 0 0.4rem 1.4rem 0 #3fa04d;
  -webkit-transform: translateY(-0.1rem);
          transform: translateY(-0.1rem);
  transition: -webkit-transform 150ms;
  transition: transform 150ms;
  transition: transform 150ms, -webkit-transform 150ms;
}

.OwnSubmit[disabled]:hover {
  cursor: not-allowed;
  box-shadow: none;
  -webkit-transform: none;
          transform: none;
}


/* BUTTONS */
.btn {
  font-size: 1.8rem;
  padding: 1rem 0;
  width: 20rem;
  text-align: center;
  border: 0.1rem solid #3fa04d;
  margin-bottom: 1rem;
  text-decoration: none;
  color: #4f7432;
  background-color: white;
  /*margin: auto;*/
}

.btn:hover {
  cursor: pointer;
  box-shadow: 0 0.4rem 1.4rem 0 #3fa04d;
  -webkit-transform: translateY(-0.1rem);
          transform: translateY(-0.1rem);
  transition: -webkit-transform 150ms;
  transition: transform 150ms;
  transition: transform 150ms, -webkit-transform 150ms;
}

.btn[disabled]:hover {
  cursor: not-allowed;
  box-shadow: none;
  -webkit-transform: none;
          transform: none;
}




#circle {
  width: 100%;
  height: 100%;
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}
#circle-percentage {
  position: absolute;
  color: #444;
  top: -3em;
  left: 0.3em;
  right: 0;
  font-size: 4em;
}
